@use 'base/variables' as v;
@use 'base/mixins' as m;

.barra {
    background-color: v.$blanco;
    padding: 1rem 2rem;
    box-shadow: 1px 12px 9px -13px rgba($color: #000000, $alpha: .4);

    @include m.tablet {
        display: flex;
        justify-content: space-between;
        align-items: center;   
    }

    p {
        margin: 0;
        font-size: 1.4rem;
        text-align: center;

        span {
            font-weight: 700;
            color: v.$indigo;
        }
    }

    .cerrar-sesion {
        @include m.boton( darken(v.$indigo, 20%), v.$blanco );
        font-size: 1.4rem;
        padding: 1rem;
        border-radius: .5rem;
        margin-top: 3rem;

        @include m.tablet {
            margin: 0;
        }
    }
}